body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #16161d;
}

.tile {
  display: -webkit-inline-flex;
  display: inline-flex;
  background-color: #4f7942;
}

.rock {
  /* Development URL */
  /* background-image: url(/tiles/rock.png); */
  /* Production URL */
  background-image: url(/dc-game/tiles/rock.png);
}

.tree {
  /* Development URL */
  /* background-image: url(/tiles/tree.png); */
  /* Production URL */
  background-image: url(/dc-game/tiles/tree.png);
}

.chest {
  /* Development URL */
  /* background-image: url(/tiles/chest.png); */
  /* Production URL */
  background-image: url(/dc-game/tiles/chest.png);
}

.container {
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.menu {
  display: grid;
  width: 300px;
}

.combat-text {
  margin-top: 5px;
  padding-top: 20px;
  border-top: 1px solid white;
  grid-column: 1 / span 2;
}

.menu-item {
  margin: auto;
  width: 100px;
  height: 100px;
  position: relative;
  grid-row: 1 / 3;
}

.player,
.enemy {
  padding: 5px;
  width: 200px;
}

.player img,
.enemy img,
.menu img {
  max-width: 100%;
  max-height: 100%;
}

h4 {
  text-align: center;
  margin: 0;
}

.menu-item div {
  width: 100%;
  position: absolute;
  bottom: 0;
}

.modal {
  position: absolute;
  top: 0;
  margin: 0 auto;
  width: 800px;
  height: auto;
  background-color: rgba(0, 0, 0, 0.8);
  border: 4px solid gray;
}

.modal h1 {
  color: white;
  margin: 0;
  padding: 0 0 4px 0;
  font-size: 2em;
  text-align: center;
  border-bottom: 1px solid gray;
}

